<template>
    <div>
        
    </div>
</template>

<script>
export default {
    name: "dashboard",
    props: ['param'],
    data() {
        return {
            ch: 200,
            cht: 200,
            
        };
    },
    methods: {
        jumpTab: function (path, title, param) {
            this.$emit('addTab', path, title, param);
        },
        resize: function () {
            let h = document.body.clientHeight;
            let mt = (h - 454) / 2;
            if (mt < 200) {
                mt = 200;
            }
            this.ch = mt;

            let cht = h - 230;
            if (cht < 200) {
                cht = 200;
            }
            this.cht = cht;
        },
        
    },
    mounted: function () {
        this.$nextTick(function () {
            let _this = this;
            this.resize();
            window.addEventListener('resize', () => {
                this.resize();
            });
        });
    },
    beforeDestroy: function () {
    },
};
</script>

<style scoped>

</style>




